<template>
	<view>
		<view style="position: fixed;
	top:0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: -1;
	" class="bg">
		</view>
		<view class="nav-bar">
			<kt-nav-bar ref="ktNavBar" id="ktNavBar" title="OVO魅力榜" backgroundColor="rgba(0, 0, 0, 0)"></kt-nav-bar>
		</view>

		<view class="box">


			<view>
				<view 
				
				v-if="false"
				style="position: relative;
			padding: 10rpx 0 10rpx 0;
			border-radius: 20rpx 20rpx 0 0;
			" class="segment-box" id="segment">

					<!-- 分段器：日榜、周榜、月榜 -->
					<view class="segment">
						<view class="segment-item" :class="{ 'segment-item-active': segmentSelect == 'day' }"
							@click="toSegmentSelect('day')">日榜</view>
						<view class="segment-item" :class="{ 'segment-item-active': segmentSelect == 'week' }"
							@click="toSegmentSelect('week')">周榜</view>
						<view class="segment-item" :class="{ 'segment-item-active': segmentSelect == 'month' }"
							@click="toSegmentSelect('month')">月榜</view>
					</view>
				</view>
				<view style="padding: 0 90rpx 0 90rpx;
							box-sizing: border-box;
							background-color: #fff;
							">
					<view style="
						background-color: rgba(118, 118, 11, 0.1);
						height: 5rpx;
						border-radius: 10rpx;
						"></view>
				</view>

				<scroll-view class="scroll-view" scroll-y :refresher-enabled="true"
					:refresher-triggered="refresherTriggered" @refresherpulling="onRefresherPulling"
					@refresherrefresh="onRefresherRefresh"
					:style="'width: 100%;height: calc(100vh -' + ' ' + navBarHeight + 'px - ' + segmentHeight + 'px - 30rpx);'">


					<view class="body-item" v-for="(item, index) in bodyData" :key="index">
						<view v-if="index > 0" style="padding: 0 90rpx 0 90rpx;
							box-sizing: border-box;
							position: relative;
							">
							<!-- <view style="
						background-color: rgba(118, 118, 118, 0.1);
						height: 4rpx;
						border-radius: 10rpx;
						padding: 50rpx;
						box-sizing: border-box;
						"></view> -->

						</view>
						<view style="height: 20rpx;"></view>

						<view style="position: relative;
						display: inline-block;
						border-radius: 60rpx;
						width: 95%;
						background-color: #fff;
						padding:20rpx;
						box-sizing: border-box;
						margin-left: 2.5%;
						">

							<user-card :ovoUserInfo="item" :distanceInShow="item.userId != userSelf.userId"></user-card>

							<image v-if="index == 0" class="jiangtou" mode="widthFix"
								:src="$kt.file.byPath('/image/icon/top1.svg')"></image>

							<image v-if="index == 1" class="jiangtou"
							 mode="widthFix" :src="$kt.file.byPath('/image/icon/top2.svg')"></image>

							<image v-if="index == 2" class="jiangtou" mode="widthFix" :src="$kt.file.byPath('/image/icon/top3.svg')"></image>

						</view>
					</view>

					<view style="height: 170rpx;"></view>
				</scroll-view>


			</view>

			<view class="my-rank">
				<user-card :distanceInShow="false" :ovoUserInfo="userSelf"></user-card>

			</view>

		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			segmentSelect: 'day',
			bodyData: [
			],
			navBarHeight: 0,
			segmentHeight: 0,
			requestParam: {
				startTimestamp: 0,
				endTimestamp: 99999999999999
			},
			refresherTriggered: false,
			oldMyRank: 0,
			myRank: 0,
			topSelf: 0,
			userSelf: {}
		}
	},
	created() {
		this.toSegmentSelect(this.segmentSelect);
		this.userSelf = this.$ovo.getSelf();
	},
	// 初始化
	mounted() {

		// 获取#ktNavBar的高度
		uni.createSelectorQuery().in(this).select('#ktNavBar').boundingClientRect((rect) => {
			this.navBarHeight = rect.height;
		}).exec();
		// 获取#segment的高度
		uni.createSelectorQuery().in(this).select('#segment').boundingClientRect((rect) => {
			this.segmentHeight = rect.height;
		}).exec();

	},

	methods: {
		onRefresherPulling() {
			this.refresherTriggered = true;
			setTimeout(() => {
				this.refresherTriggered = false;
			}, 1000);
		},
		onRefresherRefresh() {
			this.toSegmentSelect(this.segmentSelect);
		},
		toSegmentSelect(type) {
			this.segmentSelect = type;
			// 读取缓存中的数据
			var bodyData = uni.getStorageSync('charmTop_' + type);
			if (bodyData != '') {
				this.bodyData = bodyData;
			}

			if (type == 'day') {

				// 获取今日0点时间戳
				var date = new Date();
				date.setHours(0);
				date.setMinutes(0);
				date.setSeconds(0);
				date.setMilliseconds(0);
				this.requestParam.startTimestamp = date.getTime();
				this.requestParam.endTimestamp = 99999999999999;

			} else if (type == 'week') {
				// 获取本周日0点时间戳
				// 首先获取今天是周几
				var date = new Date();
				date.setHours(0);
				date.setMinutes(0);
				date.setSeconds(0);
				date.setMilliseconds(0);
				this.requestParam.startTimestamp = date.getTime() - date.getDay() * 24 * 60 * 60 * 1000;


				this.requestParam.endTimestamp = 99999999999999;

			} else if (type == 'month') {
				// 获取本月1日0点时间戳
				var date = new Date();
				date.setHours(0);
				date.setMinutes(0);
				date.setSeconds(0);
				date.setMilliseconds(0);
				date.setDate(1);
				this.requestParam.startTimestamp = date.getTime();
				this.requestParam.endTimestamp = 99999999999999;
			}
			this.getBodyData();

		},
		getCharmSelf() {
			this.oldMyRank = this.myRank;
			this.$kt.request.request({
				api: this.$api.business.ovo.userCharm.getCharmSelf,
				data: this.requestParam,
				stateSuccess: (res) => {
					this.myRank = res.data.totalCharmValue;
				}
			})
		},
		getBodyData() {
			this.$kt.request.request({
				api: this.$api.business.ovo.userCharm.getTopByTimestamp,
				data: this.requestParam,
				stateSuccess: (res) => {
					var bodyData = [];
					this.topSelf = 0;
					var data = res.data;
					for (var i = 0; i < data.length; i++) {
						var item = data[i];

						if (this.segmentSelect == 'day') {
							item.introduction = "排名: " + (i - (-1)) + " 今日魅力值: " + item.totalCharmValue
						} else if (this.segmentSelect == 'week') {
							item.introduction = "排名: " + (i - (-1)) + " 本周魅力值: " + item.totalCharmValue
						} else if (this.segmentSelect == 'month') {
							item.introduction = "排名: " + (i - (-1)) + " 本月魅力值: " + item.totalCharmValue
						}

						if (item.userId == this.userSelf.userId) {
							this.topSelf = i - (-1);
						}

						bodyData.push(item);
					}

					this.$kt.request.request({
						api: this.$api.business.ovo.userCharm.getCharmSelf,
						data: this.requestParam,
						stateSuccess: (resJson) => {
							if (resJson.data == null) {
								resJson.data = {};
								resJson.data.totalCharmValue = 0;
							}

							this.myRank = resJson.data.totalCharmValue;
							var introductionFront = "";
							if (this.segmentSelect == 'day') {
								introductionFront = "今日魅力值: " + this.myRank
							} else if (this.segmentSelect == 'week') {
								introductionFront = "本周魅力值: " + this.myRank
							} else if (this.segmentSelect == 'month') {
								introductionFront = "本月魅力值: " + this.myRank
							}

							if (this.topSelf <= 0) {
								this.userSelf.introduction = "未上榜 " + introductionFront;
							} else {
								this.userSelf.introduction = "排名: " + this.topSelf + " " + introductionFront;
							}

						}
					})


					this.bodyData = bodyData;

					// 加入缓存中
					uni.setStorageSync("charmTop_" + this.segmentSelect, this.bodyData);


				},
			});
		},
	}
}
</script>

